export default {
	data() {
		return {
			movieData: null,
			imgArr: []
		};
	},
	methods: {
		send() {
			uni.request({
				url: "https://movie.querydata.org/api?id=1302425",
				success: (res) => {
					console.log(res)
					const resobj = res.data;
					this.movieData = resobj;
				}
			})
		},
		chooseImg() {
			uni.chooseImage({
				count: 9,
				success: res => {
					this.imgArr = res.tempFilePaths
					console.log(this.imgArr)
				}
			})
		}
	}
}
